html, body { height: 100% }

body {
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
}

.overflow {
  display: block;
  overflow: hidden;
}
p {
  font-size: calculate-rem(12px);
}
ul {   padding: 0; };

//    
a {
  color: $color-primary;
  text-decoration: none;
}
a:hover { color: $color-secondary; }


.bg-grey {
  // background-color: #f0eaf1;
  background-color: lightgrey;
}

.bg-texture {
  background-image: url('../images/bg-texture.jpg');
}

.bg-white-transparent {
  background-color: rgba(255,255,255,0.5);
}

.bg-feedbacks {
  background-image: url('../images/bg-feedbacks.jpg');
  background-size: cover;
}

.conteiner-article {
  padding: 1.5em 0;
}

.widget {
  min-height: 200px;
  border: 2px solid grey;
  padding: 10px;
}

